// import libraries and moduls
import React from "react";
import "./styles.css";
import { useFormik } from "formik";
import "axios";
import { useState } from "react";
import BatchDetailsPage from "./batchdetailspage/BatchDetailsPage";
import { Form, Stack, Button } from "react-bootstrap";
const axios = require("axios");

// App default passed to index
export default function App() {
  // define all states.
  const [BatchDetails, setBatchDetails] = useState("Enter Batch Details");
  const [bottleMessage, setBottleMessage] = useState("");
  const [batchMessage, setBatchMessage] = useState("");
  const [identifiedBatchNumber, setIdentifiedBatchNumber] = useState(null);
  const [identifiedBottleNumber, setIdentifiedBottleNumber] = useState(null);
  const [showPage, setShowPage] = useState(false);

  // formik function
  const formik = useFormik({
    // formik initial values : bottlenumber and batch number so
    // starts with nothing showing
    initialValues: {
      bottlenumber: "",
      batchnumber: ""
    },
    // WHEN SUBMIT BUTTON IS PRESSED
    onSubmit: async (values) => {
      // define batchnumber  as the value input in the form
      // batch input at the time of pressing Submit button
      setShowPage(false);
      var batchnumber = formik.values.batchnumber;
      // send query to database with batch number
      await axios
        .get(
          `https://perfumetracker-default-rtdb.europe-west1.firebasedatabase.app/batches/${batchnumber}.json`
        )
        .then(function (result) {
          // if there is no data in db then response.data will be null
          if (result.data === null) {
            // no batch number data found
            // hide page - showPage(false)
            setShowPage(false);
            // set batchDetails to null. this is not strictly
            // necessary as the <BatchDetailsPage /> should not be
            // called if ShowPage state is false
            setBatchDetails(null);
            // display message - Batch not found
            setBatchMessage("Batch Not Found");
            // do not show any bottle message
            setBottleMessage("");
          } else {
            // set BatchDetails state to the the details returned
            // from the database
            setBatchDetails(result.data);
            // show batch message
            setBatchMessage(`Batch Identified: ${batchnumber}`);
            // check if the bottle entered is in the batch.
            // call checkBottle function.
            // checkBottle function takes in the result data
            // and checks if the bottle number is included in the
            // batch bottles list.
            // if true, then:
            //     1. set bottle message to succes
            //     2. set IdentifiedBottleNumber to bottle number form
            //        so this bottlenumber can be passed to <BatchDet.. />
            //     3. ShowPage state to true so the <BatchDetailsPage/>
            //        is called
            // if false, then:
            //     1. set bottle message to say that bottle not found.
            //     2. set show page to false so <BatchDetailsPage />
            //        is hidden (if it was previously showing)

            checkBottle(result.data);
            // set the state identifiedBatchNumber to the batch
            // batch number in the form input.
            setIdentifiedBatchNumber(batchnumber);
          }
        });
    }
  });

  // FUNCTION THAT checks bottle in batch and generate message BOTTLE MESSAGE

  function checkBottle(batchDetailsInGenBotMes) {
    var bottleNumbersInGenBotMes = Object.values(
      batchDetailsInGenBotMes.bottleNumbers
    );

    if (
      bottleNumbersInGenBotMes.includes(
        parseInt(formik.values.bottlenumber, 10)
      )
    ) {
      setBottleMessage(`Bottle Identified: ${formik.values.bottlenumber}`);
      setIdentifiedBottleNumber(formik.values.bottlenumber);
      setShowPage(true);
    } else {
      setBottleMessage("Bottle Not Found");
      setShowPage(false);
    }
  }

  // RETURN

  return (
    <div className="App">
      {/*  FORM  */}
      <div style={{ padding: "5%" }}>
        <Form onSubmit={formik.handleSubmit}>
          <Stack direction="horizontal" gap={3}>
            <Form.Control
              id="bottlenumber"
              name="bottlenumber"
              type="number"
              onChange={formik.handleChange}
              value={formik.values.bottlenumber}
              className="me-auto"
              placeholder="Bottle Number..."
            />
            <Form.Control
              id="batchnumber"
              name="batchnumber"
              type="text"
              onChange={formik.handleChange}
              value={formik.values.batchnumber}
              placeholder="Batch Number..."
            />
            <Button variant="secondary" type="submit">
              Submit
            </Button>
          </Stack>
        </Form>
        {/*  END FORM  */}
        <br />
        {/*  batch message and bottle message  */}

        <p>{batchMessage}</p>
        <p>{bottleMessage}</p>

        <br />
        {/*  BATCH DETAIL PAGE - SEND IDENTIFIED BATCH NUMBER WHICH IS NULL UNLESS SUCCESSFULLY FOUND IN THE DB  */}

        {/*  if showPage is true then show Batch details.  */}
        {/*  otherwise show blank <p></p> */}

        {showPage ? (
          <BatchDetailsPage
            batchDetailsData={BatchDetails}
            batch={identifiedBatchNumber}
            bottlenumber={identifiedBottleNumber}
          />
        ) : (
          <p></p>
        )}
      </div>
    </div>
  );
}
