export default function BottleNumbersInBatch(props) {
  return (
    <>
      <ul className="ula" style={{ "list-style-type": "none" }}>
        {props.bottleNumbers.map((number) => (
          <li key={number} style={{ display: "inline", listStyleType: "none" }}>
            <a>{number} </a>
          </li>
        ))}
      </ul>
    </>
  );
}
