import { Row, Col, Container, Card } from "react-bootstrap";

export default function COLXL(props) {
  return (
    <Col xs={12} sm={12} md={12} lg={12}>
      <Card id="redCard" className="Card" style={{ margin: "10px" }}>
        <h6 className="h6">{props.title}</h6>
        <p>{props.component}</p>
      </Card>
    </Col>
  );
}
