import { Row, Col, Container, Card } from "react-bootstrap";

export default function COLSmall(props) {
  return (
    <Col xs={12} sm={6} md={6} lg={3}>
      <Card id="blueCard" className="Card" style={{ margin: "10px" }}>
        <h6 className="h6">{props.title}</h6>
        <h3>{props.component}</h3>
      </Card>
    </Col>
  );
}
