import {
  ResponsiveContainer,
  Bar,
  BarChart,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";
import { createClass } from "react";

const data = [
  {
    name: "Lavender",
    "%": 100,
    fill: "#C58BF0"
  },
  {
    name: "Aromatic",
    "%": 90,
    fill: "#23BD91"
  },
  {
    name: "Citrus",
    "%": 72,
    fill: "#FFFC4A"
  },

  {
    name: "Vanilla",
    "%": 65,
    fill: "#F5E2B4"
  },
  {
    name: "Amber",
    "%": 62,
    fill: "#FFA958"
  },
  {
    name: "Woody",
    "%": 58,
    fill: "#713700"
  },
  {
    name: "Powdery",
    "%": 51,
    fill: "#FFFCE7"
  }
];

export default function ScentChart() {
  return (
    <ResponsiveContainer width={"98%"} height={300}>
      <BarChart
        width={500}
        height={300}
        data={data}
        margin={{
          top: 10,
          right: 30,
          left: 30,
          bottom: 5
        }}
      >
        <Tooltip />
        <XAxis interval={0} dataKey="name" />
        <Bar dataKey="%" />
      </BarChart>
    </ResponsiveContainer>
  );
}
