export default function SpecialistVarieties(props) {
  var varieties = props.varieties;
  return (
    <>
      <br />
      <ul>
        {Object.keys(varieties).map((variety) => (
          <li key={variety} style={{ listStyleType: "none" }}>
            <h2>{variety}</h2>
            <p>{varieties[variety]}</p>
          </li>
        ))}
      </ul>
    </>
  );
}
