import COLSmall from "./components/ContRowCol";
import BottleNumbersInBatch from "./components/bottleNumbersInBatch";
import Overview from "./components/overview";
import { Container, Row, Col, Card } from "react-bootstrap";
import COLLarge from "./components/ColLarge";
import ScentChart from "./components/scentChart";
import Notes from "./components/Notes";
import SpecialistVarieties from "./components/SpecialistVarieties";
import HeroSection from "./components/HeroSection";
import COLXL from "./components/ColXL";
import PerfumeDescription from "./components/perfumeDescription";
import Description from "./components/Description";
import COLSmallIngredient from "./components/COLSmallIngredient";

export default function BatchDetailsPage(props) {
  // COLLATE DATA
  var batchDetailsData = props.batchDetailsData;
  var bottleNumbers = batchDetailsData.bottleNumbers;
  var dateOfBottling = batchDetailsData.dateOfBottling;
  var dateOfCompounding = batchDetailsData.dateOfCompounding;
  var dateOfHarvesting = batchDetailsData.dateOfHarvesting;
  var harvester = batchDetailsData.harvester;
  var nameOfPerfume = batchDetailsData.name;
  var batch = props.batch;
  var keyCrop = batchDetailsData.keyCrop;
  var bottleNumber = props.bottlenumber;
  var specialistVarieties = props.batchDetailsData.specialistVarieties;
  var notesData = props.batchDetailsData.notes;
  var perfumeDescription = batchDetailsData.perfumeDescription;
  var perfumeImage = batchDetailsData.image;
  var imageDistilling = batchDetailsData.imageDistilling;
  var imageHarvesting = batchDetailsData.imageHarvesting;
  var imageCompounding = batchDetailsData.imageCompounding;
  var imageBottling = batchDetailsData.imageBottling;
  var description = batchDetailsData.description;
  var ingredients = batchDetailsData.ingredients;

  return (
    <>
      <Container>
        <HeroSection />
      </Container>

      <Container>
        {/* OVERVIEW - NAME - HOUSE */}
        <hr />
        <Row className="justify-content-md-center">
          <Col
            className="subtitlePadding"
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <h1>Batch</h1>
          </Col>{" "}
          <COLSmall
            id="woodyCard"
            spac="2"
            title="Verification"
            component={
              <>
                <div style={{ display: "inline-block" }}>
                  <p>Verified</p>
                </div>
              </>
            }
          />
          <COLSmall
            id="woodyCard"
            spac="2"
            title="Your Bottle is"
            component={
              <p>
                {bottleNumber}/{bottleNumbers.length}
              </p>
            }
          />
          <COLLarge
            title="Bottles in Batch"
            component={<BottleNumbersInBatch bottleNumbers={bottleNumbers} />}
          />
        </Row>
        <hr />
        <Row className="justify-content-md-center">
          {/* OVERVIEW */}
          <Col
            className="subtitlePadding"
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <h1>Perfume</h1>
          </Col>

          <Col xs={12} sm={12} md={6} lg={6}>
            <Card className="Card" style={{ margin: "10px" }}>
              <img src={perfumeImage} />
            </Card>
          </Col>

          <COLSmall spac="2" title="Name" component={<p>{nameOfPerfume}</p>} />

          <COLSmall
            spac="2"
            title="Manufactured by"
            component={<p>Scottish Lavender Oils</p>}
          />

          <COLSmall
            spac="2"
            title="Compounded on"
            component={<p>{dateOfCompounding}</p>}
          />

          <COLSmall
            spac="2"
            title={dateOfCompounding}
            component={<img width="100%" src={imageCompounding} />}
          />

          <COLSmall
            spac="2"
            title="Bottled on"
            component={<p>{dateOfBottling}</p>}
          />

          <COLSmall
            spac="2"
            title={dateOfBottling}
            component={<img width="100%" src={imageBottling} />}
          />

          <COLXL
            title="Description"
            component={<Description text={description} />}
          />

          {/* Scent profile */}
          {/* OVERVIEW */}
        </Row>

        <hr />
        <Row className="justify-content-md-center">
          <Col
            className="subtitlePadding"
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <h1>Specialist Crops</h1>
          </Col>
          <COLSmall
            id="lavenderCard"
            spac="2"
            title="Specialist Accord"
            component={<p>{keyCrop}</p>}
          />
          <COLSmall
            id="woodyCard"
            spac="2"
            title="Harvested & Distilled on"
            component={<p>{dateOfHarvesting}</p>}
          />
          <COLSmall
            id="woodyCard"
            spac="2"
            title="Harvested by"
            component={<p>{harvester}</p>}
          />
          <COLSmall
            id="woodyCard"
            spac="2"
            title="Grown at"
            component={<p>Scottish Lavender Oils, Tarhill Farm, Kinross</p>}
          />
          <COLSmall
            id="woodyCard"
            spac="2"
            title={<p>{dateOfHarvesting}</p>}
            component={
              <>
                <img width="100%" src={imageHarvesting} />
              </>
            }
          />

          <COLSmall
            id="woodyCard"
            spac="2"
            title={<p>{dateOfHarvesting}</p>}
            component={<img width="100%" alt="" src={imageDistilling} />}
          />

          <COLXL
            title="Ingredients grown on our farm"
            component={<SpecialistVarieties varieties={specialistVarieties} />}
          />
        </Row>
        <hr />

        <Row className="justify-content-md-center">
          <Col
            className="subtitlePadding"
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <h1>Partner Farms</h1>
          </Col>

          {console.log(ingredients)}
          {Object.keys(ingredients).map((ing) => (
            <COLSmallIngredient
              id="lavenderCard"
              spac="2"
              title={ingredients[ing].plant}
              origin={ingredients[ing].origin}
              component={
                <>
                  <p>{ingredients[ing].description}</p>
                </>
              }
            />
          ))}
        </Row>

        <hr />

        <Row className="justify-content-md-center">
          <Col
            className="subtitlePadding"
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
          >
            <h1>Perfume Profile</h1>
          </Col>
          <COLXL
            title="Marina's View from Italy"
            component={
              <PerfumeDescription perfumeDescription={perfumeDescription} />
            }
          />
          <COLLarge component={<ScentChart />} />
          <COLLarge
            title="Top Notes"
            component={<Notes notes={notesData.top} />}
          />
          <COLLarge
            title="Heart Notes"
            component={<Notes notes={notesData.heart} />}
          />
          <COLLarge
            title="Base Notes"
            component={<Notes notes={notesData.bottom} />}
          />
          <COLLarge
            title="Note"
            component={
              <p>
                The scents reflected above do not necessarily correspond to the
                ingredients used. Different ingredients can change scent when
                mixed.
              </p>
            }
          />

          <COLLarge
            title="Perfume Video"
            component={
              <video
                controls
                width="100%"
                src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/output_compress-video-online.com_bedf781f-9ec4-4a96-a808-10fa0c84a111.mp4?v=1643733267"
              />
            }
          />
        </Row>
        {/* OVERVIEW - REST */}
      </Container>
    </>
  );
}
